</code></pre>
<p><a href="https://lodash.com/docs/#debounce">详细文档</a></p>
<h2>throttle</h2>
<pre class="hljs"><code><span class="hljs-keyword">import</span> { throttle } <span class="hljs-keyword">from</span> <span class="hljs-string">'vux'</span>

